.projectsBlock {
    min-height: 100vh;
    background-color: #e3f2ff;
}
.projectsContainer{
    flex-direction: column;
    justify-content: flex-start;
}
.title {
    background-color: white;
}
.projects {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}